@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer>section>article:first-child{
  margin-right: 4rem;
}

.App-logo {
  pointer-events: none;
}

.hamburger{
  cursor: pointer;
  position: absolute;
  color: var(--main-color);
  top: 0;
  right: 0;
  transition: 0.25s;
}
.hamburger:hover{
  color : var(--select-color);
}

/*애니메이션 동작 축소*/
@media (prefers-reduced-motion: no-preference) {
}

.App-header {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: var(--main-color);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media all and (min-width:1441px){
  .App-header {
    padding-top: 12rem;
  }
  .App-logo {
    width: 2.75rem;
    height: 2.75rem;
  }
  .hamburger{
    padding: 3rem 3.5rem;
    width: 2.75rem;
    height: 2.75rem;
  }
  .footer{
    display: flex;
    padding-top: 4rem;
  }
  .footer>*:first-child{
    flex: 2 1;
  }
  .footer>*:last-child{
    flex: 1 1;
  }
}
@media all and (max-width:1440px){
  .App-header {
    padding-top: 10rem;
  }
  .App-logo {
    width: 2rem;
    height: 2rem;
  }
  .hamburger{
    padding: 3rem 3.5rem;
    width: 2.25rem;
    height: 2.25rem;
  }
  .footer{
    display: flex;
    padding-top: 4rem;
  }
  .footer>*:first-child{
    flex: 2 1;
  }
  .footer>*:last-child{
    flex: 1 1;
  }
}
@media all and (max-width:1024px){
  .App-header {
    padding-top: 8rem;
  }
  .hamburger{
    padding: 2.5rem 3rem;
    width: 2rem;
    height: 2rem;
  }
}
@media all and (max-width:768px){
  .App-header {
    padding-top: 8rem;
  }
  .footer{
    display: flex;
    flex-direction: column-reverse;
    padding-top: 4rem;
  }
  .footer>*:first-child{
    flex: 1 1;
  }
  .footer>*:last-child{
    flex: 1 1;
  }
}
@media all and (max-width:425px){
  .hamburger{
    padding: 1.25rem 1.75rem;
    width: 1.5rem;
    height: 1.5rem;
  }
}
:root{
    --main-color: #585858;
    --main-color-75: #585858c0;
    --main-color-50: #58585880;
    --main-color-25: #58585840;
    --select-color: #f2849e;
}

*{
    -webkit-tap-highlight-color: transparent;
}

section{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
article{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
article>div.flex{
    display: flex;
    flex-direction: column;
}

p, h1, h2, h3, h4, h5, label{
    color: #585858;
    color: var(--main-color);
}

h1, h5{
    font-weight: 700;
}
h2, h3, h4, b, strong{
    font-weight: 800;
}
h2, h3, h4{
    letter-spacing: 0.5rem;
}
i, em, sup, sub{
    font-weight: 300;
    color: #585858c0;
    color: var(--main-color-75);
}
blockquote{
    margin: 2rem 0 2rem 0;
    padding: 1rem 0rem 1rem 3rem;
    font-style: italic;
    font-weight: 300;
    line-height: 2rem;
    border-left: 4px solid #58585840;
    border-left: 4px solid var(--main-color-25);
}
pre{
    padding: 2rem 2rem 2rem 2rem;
    margin-bottom: 2rem;
    border:  1px solid #58585840;
    border:  1px solid var(--main-color-25);
    border-radius: 0.25rem;
}
p, label{
    line-height: 1.75rem;
    vertical-align: baseline;
}
p.left, p.left>*{
    float: left;
    line-height: 2.25rem;
}
p.right, p.right>*{
    float: right;
    line-height: 2.25rem;
}
p.left>*, p.right>*{
    width: 11rem;
}
p.left>*{
    margin: 0rem 2rem 0.25rem 0rem;
}
p.right>*{
    margin: 0rem 0rem 0.25rem 2rem;
}
span{
    cursor: pointer;
    color: #585858;
    color: var(--main-color);
    transition: 0.25s;
}
span:hover{
    color: #f2849e;
    color: var(--select-color);
}
a{
    transition: 0.25s;
    color: #585858;
    color: var(--main-color);
    text-decoration: none;
    border-bottom: 1px dotted #585858;
    border-bottom: 1px dotted var(--main-color);
}
a:hover{
    color: #f2849e;
    color: var(--select-color);
    border-color: transparent;
}
hr{
    border-top: 1px solid #58585880;
    border-top: 1px solid var(--main-color-50);
}
hr.blank{
    border: 0;
}
ul>li>ul{
    padding: 0 !important;
}
ul.alt, ul.alt_no{
    position: relative;
    padding: 1rem 0rem 1rem 0rem;
    list-style: none;
}
ul.alt:after{
    position: absolute;
    display: block;
    content: " ";
    top: 0px;
    left: 0px;
    width: calc(100% - 1rem);
    height: calc(100% - 2rem);
    margin: 1rem 0rem 1rem 0rem;
    box-sizing: border-box;
    border: 2px solid white;
    pointer-events: none;
}
ul.alt>li{
    margin-right: 1rem;
    box-sizing: border-box;
    border: 1px solid #58585840;
    border: 1px solid var(--main-color-25);
    padding: 0.5rem 0rem;
}
ul.alt_no>li{
    padding: 0.5rem 0rem;
}
li svg{
    background-color: #585858;
}
ul.horizontal{
    display: flex;    
    padding: 1rem 1rem 1rem 0rem;
}
ul.horizontal>li{
    padding: 0.5rem 1.5rem 0.5rem 0rem;
    list-style: none;
}
ul.horizontal>li:last-child{
    padding-right: 0rem;
}
ul.horizontal.alt{
    -webkit-transform: translateX(-12.5%) scale(0.75);
            transform: translateX(-12.5%) scale(0.75);
}
ul.horizontal.alt>li{
    border: 0;
    display: flex;
}
ul.horizontal.alt>li:not(:last-child){
    border-right: 1px solid #58585880;
    border-right: 1px solid var(--main-color-50);
}
ul.horizontal.alt>li p, ul.horizontal.alt>li a{
    color: #58585880;
    color: var(--main-color-50);
}
ul.horizontal.alt>li a:hover{
    color: #f2849e;
    color: var(--select-color);
}
ul.fit{
    display: flex;
    width: 100%;
    justify-content: space-between;
}
ul.fit>li{
    display: flex;
    align-items: center;
    flex: 1 1;
}
ul.fit>li>button{
    flex: 1 1;
}
ul.flex{
    flex-wrap: wrap;
}
.borderbox{
  padding: 1rem;
  border: 1px solid #58585840;
  border: 1px solid var(--main-color-25);
  border-radius: 0.25rem;
}
li>p{
    margin-bottom: 0;
}

div.fit{
    display: flex;
    width: 100%;
}
div.fit>*{
    flex: 1 1;
}

body>div{
    max-width: 1345px;
    margin: 0px auto;
    padding: 0px 8rem;
}
button.fit{
    width: 100%;
}
button, button.default, input[type="reset"], input[type="submit"]{
    display: flex;
    align-items: center;
    border-radius: 0.25rem;
    border: 2px solid #585858;
    border: 2px solid var(--main-color);
    background-color: white;
    transition: 0.5s;
}
button.primary, input[type="submit"]{
    background-color: #585858;
    background-color: var(--main-color);
}
button>a>p, button.primary>a>p{
    transition: 0.5s;
    margin: 0;
    font-weight: 700;
    letter-spacing: 0.25rem;
    margin-right: -0.25rem;
}
button.primary>a>p, input[type="submit"]{
    color: white;
}
button>a{
    flex: 1 1;
}
button>a>p{
    text-align: center;
    color: #585858;
    color: var(--main-color);
}
button:not(.disabled):hover, button:not(.disabled):hover>a>p, 
button.default:not(.disabled):hover, button.default:not(.disabled):hover>a>p,
input[type="reset"]:hover{
    color: #f2849e;
    color: var(--select-color);
}
button.primary:not(.disabled):hover, input[type="submit"]:hover{
    background-color: #f2849e;
    background-color: var(--select-color);
}
button.primary:not(.disabled):hover>a>p, input[type="submit"]:hover{
    color: white;
}
button.primary:not(.disabled):hover, button:not(.disabled):hover, button.default:not(.disabled):hover
, input[type="submit"]:hover, input[type="reset"]:hover{
    border: 2px solid #f2849e;
    border: 2px solid var(--select-color);
}
button svg{
    transition: 0.5s;
    margin-right: 0.5rem;
}
button.primary svg{
    background-color: white;
}
button svg, button a{
    float: left;
    border-bottom: 0px;
}
button:not(.disabled):hover svg, button.default:not(.disabled):hover svg{
    background-color: #f2849e;
    background-color: var(--select-color);
}
button.primary:not(.disabled):hover svg{
    background-color: white;
}
button.disabled{
    opacity: 0.5;
}
button.disabled a{
    cursor: default;
}

header svg:first-child{
    margin-left: -0.75rem;
    padding: 0.75rem 0.75rem;
}

span{
    font-size: 1rem;
    font-weight: 700;
}

table{
    width: 100%;
    border-spacing: 0px;
    border-collapse: collapse;
}
thead{
    color: #585858;
    color: var(--main-color);
}
thead th, tbody td{
    text-align: start;
    font-size: 1.2rem;
    padding: 1.1rem;
}
tbody td{
    color: #585858c0;
    color: var(--main-color-75);
}
table tr:last-child>th, table tr:nth-last-child(2)>td{
    border-bottom: 2px solid #58585840;
    border-bottom: 2px solid var(--main-color-25);
}
table tr>td{
    border-bottom: 1px solid #58585840;
    border-bottom: 1px solid var(--main-color-25);
}
table tr:last-child>td{
    border-bottom: 0;
}
table.alt>tbody>tr>td{
    border: 1px solid #58585840;
    border: 1px solid var(--main-color-25);
}
table.alt>thead>tr:last-child>th ,table.alt>tbody>tr:last-child>td{
    border: 0;
}
select{
    margin-top: 2rem;
    width: 100%;
}
select, input, textarea{
    border: 0px;
    box-sizing: border-box;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #58585880;
    border-bottom: 1px solid var(--main-color-50);
    color: #585858;
    color: var(--main-color);
    font-weight: 400;
}
select:focus-visible, input:focus-visible, textarea:focus-visible{
    border: 0px;
    box-sizing: border-box;
    outline: none;
    margin-bottom: -1px;
    border-bottom: 2px solid #f2849e;
    border-bottom: 2px solid var(--select-color);
}
label{
    cursor: pointer;
}
input[type="radio"], input[type="checkbox"]{
    margin-right: 1rem;
}
input[type="radio"]::before{
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    content: url(/Phantom/static/media/check-solid.2f94d1e5.svg);
    position: relative;
    top: -0.375rem;
    left: -0.375rem;
    color: white;
    background-color: white;
    border-radius: 50%;
    border:  1px solid #585858;
    border:  1px solid var(--main-color);
}
input[type="radio"]:checked::before{
    background-color: #585858;
    background-color: var(--main-color);
    border:  1px solid #585858;
    border:  1px solid var(--main-color);
}
input[type="checkbox"]::before{
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    content: url(/Phantom/static/media/check-solid.2f94d1e5.svg);
    position: relative;
    top: -0.375rem;
    left: -0.375rem;
    color: white;
    background-color: white;
    border-radius: 5px;
    border:  1px solid #585858;
    border:  1px solid var(--main-color);
}
input[type="checkbox"]:checked::before{
    background-color: #585858;
    background-color: var(--main-color);
    border:  1px solid #585858;
    border:  1px solid var(--main-color);
}
textarea{
    width: 100%;
    margin-top: 1rem;
    resize: none;
}
img{
    width: 99%;
    border-radius: 0.25rem;
}

