.imganime{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.imganime>a{
    position: absolute;
    width: 90%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 0;
    z-index: 2;
}
.imganime>a>h2{
    color: white;
    text-align: center;
    margin-bottom: 1rem;
    -webkit-transform: translateY(140%);
            transform: translateY(140%);
    transition: 0.25s 0.125s ;
}
.imganime>a>p{
    color: transparent;
    text-align: center;
