aside{
    position: absolute;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: var(--main-color);
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.5s;
    z-index: 100;
}
aside>svg{
    cursor: pointer;
    position: absolute;
    color: transparent;
    visibility: hidden;
    top: 0;
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
    transition: ease-in 0.25s;
} 
aside.active>svg{
    visibility:visible;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    transition: cubic-bezier(0.75, 0, 1, 1) 0.25s;
    color: var(--main-color);
}
aside.active>svg:hover{
    visibility:visible;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    color: var(--select-color);
}
aside.active{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}
aside>div{
    position: absolute;
    top: 0;
    right: 100%;
    width: 100vw;
    height: 100vh;
    color: transparent;
    visibility: hidden;
    z-index: -1;
    transition: 0.5s;
}
aside.active>div{
    width: calc(100vw - 100%);
    visibility:visible;
    background-color: rgba(255, 255, 255, 0.75);
}

aside h2{
    color: white;
}
aside ul.alt{
    padding: 0;
}
aside ul.alt:after{
    border: 2px solid var(--main-color);
}
aside ul.alt li{
    padding: 1.125rem 0rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.125);
}
aside ul.alt li a{
    color: white;
    line-height: 1.5;
}
aside ul.alt li a:hover{
    color: var(--select-color);
    line-height: 1.5;
}

